export default{
compressImg(imgData,maxHeight,onCompress){
  if(!imgData)return;
  onCompress = onCompress || function(){};

  maxHeight = maxHeight || 200;//200px

  var canvas = document.createElement('canvas');
  var img = new Image();
  img.onload = function(){
    if(img.height > maxHeight) {//
      img.width *= maxHeight / img.height;
      img.height = maxHeight;
    }
    var ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height); // canvas
    //canvans
    canvas.width = img.width;
    canvas.height = img.height;
    ctx.drawImage(img, 0, 0, img.width, img.height); // canvas
    onCompress(canvas.toDataURL("image/jpeg"));//canvascanvas
  };
  // srcimgcanvas
  img.src = imgData;
}
}



