import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import Index from '@/components/Home/Index'
import Notice from '@/components/Home/Notice'
import User from '@/components/Home/User'
import Info from '@/components/user/Info'
import updateInfo from '@/components/user/UpdateInfo'
import Pay from '@/components/user/Pay'
import Uppass from '@/components/user/Uppass'
import Score from '@/components/user/Score'
import ScoreDetail from '@/components/user/ScoreDetail'
import Life from '@/components/life/Life'
import Today from '@/components/Today'

import Interaction from '@/components/Interaction'
import InteractionDetail from '@/components/InteractionDetail'
import Discuss from '@/components/life/Discuss'
import Findorg from '@/components/life/Findorg'

import StudyDetail from '@/components/life/StudyDetail'
import Summary from '@/components/life/Summary'
import PersonalSummary from '@/components/life/PersonalSummary'
import AuditStatus from '@/components/life/AuditStatus'
import Thought from '@/components/life/Thought'
import Members from '@/components/life/Members'
import Review from '@/components/life/Review'
import LifeChoice from '@/components/life/LifeChoice'
import Login from '@/components/Login'

/**/
import Newseye from '@/components/news/Newseye'//
import Study from '@/components/news/Study'//
import Oneclick from '@/components/news/Oneclick'//
import Anytimestudy from '@/components/news/AnytimeStudy'/**/
import Anytimephoto from '@/components/news/AnytimePhoto'/**/
import System from '@/components/news/System'/**/
import Activity from '@/components/news/Activity'/**/
import ShowIdentity from '@/components/news/ShowIdentity'/**/
import  NewsDetail from '@/components/news/Newsdetail'

Vue.use(Router)
let route= new Router({
  linkActiveClass:'active',
  routes: [
    /**/
    {
      path: '/',
      component: Home,
      children:[
        {
          path: '',
          component: Index
        },
        {
          path: '/index',
          component: Index
        },
        {
          path: '/notice',
          component: Notice
        },
        {
          path: '/user',
          component: User,
        },
      ]
    },
    /**/
    {
      path: '/life',
      name: 'life',
      component: Life
    },
    /**/
    {
      path: '/review/:other_user_id/:id',
      name: 'review',
      component: Review
    },
    /**/
    {
      path: '/interaction',
      name: 'interaction',
      component: Interaction,
      meta: {
        requireAuth: true,  // 
      },
    },
    // 
    {
      path: '/interactionDetail',
      name: 'interactionDetail',
      component: InteractionDetail,
      meta: {
        requireAuth: true,  // 
      },
    },

    /**/
    {
      path: '/oneclick',
      name: 'oneclick',
      component: Oneclick
    },

    /**/
    {
      path: '/today',
      name: 'today',
      component: Today
    },
    /**/
    {
      path: '/showidentity',
      name: 'showidentity',
      component: ShowIdentity
    },
    /**/
    {
      path: '/newseye',
      name: 'newseye',
      component: Newseye
    },
    /**/
    {
      path: '/anytimestudy',
      name: 'anytimestudy',
      component: Anytimestudy,
    },
    /**/
    {
      path: '/anytimephoto',
      name: 'anytimephoto',
      component: Anytimephoto
    },

    /**/
    {
      path: '/System/',
      name: 'System',
      component: System
    },

    /**/
    {
      path: '/activity/',
      name: 'activity',
      component: Activity
    },



    /**/
    {
      path: '/uppass',
      name:'uppass',
      component: Uppass,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/info',
      name:'info',
      component: Info,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/updateinfo',
      name:'updateinfo',
      component: updateInfo,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/score',
      name:'score',
      component: Score,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/scoredetail',
      name:'scoredetail',
      component: ScoreDetail,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/pay',
      name:'pay',
      component: Pay,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/discuss',
      name:'discuss',
      component: Discuss,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/LifeChoice',
      name:'LifeChoice',
      component: LifeChoice,
    },
    /**/
    {
      path: '/Members',
      name:'Members',
      component: Members,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/findorg',
      name:'findorg',
      component: Findorg
    },
    /**/
    {
      path: '/study',
      name:'study',
      component: Study
    },
    /**/
    {
      path: '/studydetail/:id',
      name:'studydetail',
      component: StudyDetail
    },
    /**/
    {
      path: '/summary',
      name:'summary',
      component: Summary,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/personalsummary',
      name:'personalsummary',
      component: PersonalSummary,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/thought',
      name:'thought',
      component: Thought,
      meta: {
        requireAuth: true,  // 
      },
    },
    /**/
    {
      path: '/auditStatus/:state/:type',
      name:'auditStatus',
      component: AuditStatus
    },
    /**/
    {
      path: '/login',
      name:'login',
      component: Login
    },
    /**/
    {
      path:'/newsDetail/:newsId',
      name:'newsDetail',
      component: NewsDetail
    }
],

})

export default route




